import React, { Component } from 'react'
import BodyRight from '../BodyRight'

export default class Start extends Component {
    render() {
        return (
            <div className="all">
                <div className="body">
                    <div className="body_left1">
                        <div className="loginheader">
                            <span className="lh_1">主页</span>
                            <span>/</span>
                            <span>登录</span>
                        </div>
                        <div className="loginui">
                            <div className="uitop">
                                <span>用户名</span>
                                <input></input>
                            </div>
                            <div className="uibottom">
                                <span>密码</span>
                                <input></input>
                            </div>
                            <div className="uibutton">
                                <button onClick={()=>this.props.history.push("/")}>登录</button>
                            </div>
                        </div>
                    </div>
                    <BodyRight />
                </div>
            </div>
        )
    }
}