import React, { Component } from 'react'
import BodyRight from '../BodyRight'

export default class Start extends Component {
    constructor() {
        super();
        this.state = {
            data: []
        }
    }
    componentDidMount() {
        let id = this.props.location.pathname.slice(9);
        fetch('https://cnodejs.org/api/v1/topic/' + id)
            .then((res) => res.json())
            .then((res) => {
                this.setState({
                    data: res.data
                });
            })
    }
    render() {
        console.log(111);
        return (
            <div className="all">
                <div className="body">
                    <div className="body_left">
                        <div className="conhead">
                            <h2>
                                {this.state.data.title}
                            </h2>
                            <span>·发布于1个月前 · 作者atian · {this.state.data.visit_count}次浏览 · 来自 分享</span>
                        </div>
                        <div className="conbd" dangerouslySetInnerHTML={{ __html: this.state.data.content }}></div>
                    </div>
                    <BodyRight />
                </div>
            </div>
        )
    }
}