import React, { Component } from 'react'
import Nomatch from './containers/Nomatch';
import All from './containers/All';
import Cream from './containers/Cream';
import Share from './containers/Share';
import Qaa from './containers/Qaa';
import Advertises from './containers/Advertises';
import { BrowserRouter as Router, Route, Switch, Link, Redirect } from 'react-router-dom';
import Test from './containers/Test';
import Content from './containers/Qaa';

export default class BodyLeft extends Component {
    render() {
        return (
            <Router>
                <div className="body_left">
                    <div className="body_left_nav">
                        <div className="body_left_nav_div">
                            <Link to="/">全部</Link>
                        </div>
                        <div className="body_left_nav_div">
                            <Link to="/cream">精华</Link>
                        </div>
                        <div className="body_left_nav_div">
                            <Link to="/share">分享</Link>
                        </div>
                        <div className="body_left_nav_div">
                            <Link to="/qaa">问答</Link>
                        </div>
                        <div className="body_left_nav_div">
                            <Link to="/advertises">招聘</Link>
                        </div>
                        <div className="body_left_nav_div">
                            <Link to="/test">客户端测试</Link>
                        </div>
                        <Switch>
                            <Route exact path='/' component={All} />
                            <Route path='/cream' component={Cream} />
                            <Route path='/share' component={Share} />
                            <Route path='/qaa' component={Qaa} />
                            <Route path='/advertises' component={Advertises} />
                            <Route path='/test' component={Test} />
                            <Route path='/nomatch' component={Nomatch} />
                            {/* <Redirect to='/nomatch' /> */}
                        </Switch>
                    </div>
                </div>
            </Router>
        )
    }
}
